// import axios from 'axios';

const state = {
	address: null,
	public: null,
	private: null,
};

const getters = {
	isLogin: state => (!!state.address && !!state.address.length),
};

const mutations = {
	SET_WALLET: (state, payload) => {
		state.address = payload.address;
		state.public = payload.public;
		state.private = payload.private;
	},
	LOGOUT: async (context, payload) => {
		localStorage.removeItem('wallet');
		state.address = null;
		state.public = null;
		state.private = null;
	},
};

const actions = {
	SET_WALLET: async (context, payload) => {
		localStorage.setItem('wallet', JSON.stringify(payload));
		context.commit('SET_WALLET', payload);
	},
};

export default {
	state,
	getters,
	mutations,
	actions,
};